/* *** Custom Pages *** */

/* Home */
@screen lg {
  .home {
    h1 {
      @apply !text-[140px] !leading-[.8] !mb-sp-10;
    }
    h4,
    .not-heading-h4 {
      font-size: 20px;
      line-height: 110%;
      letter-spacing: 0.2px;
      margin-bottom: 0;
    }
    .features-block-callout {
      > p {
        @apply text-[20px] leading-[1.6] font-heading;
      }
    }
    .home-testimonials {
      p {
        @apply text-[14px] leading-[1.6] font-heading;
      }
    }
  }
}
.home {
  h1 {
    @apply text-[42px] leading-[1.1] mb-sp-4 font-heading;
  }
  h2 {
    @apply text-[32px] leading-[1.1] mb-sp-8 font-heading-serif;
    @screen lg {
      @apply text-[40px];
    }
  }
  h3 {
    font-size: 20px;
    line-height: 120%;
    letter-spacing: 0.2px;
    @screen lg {
      @apply text-[28px] leading-[1.1];
    }
  }
  p {
    @apply text-[14px] leading-[1.6] font-heading;
  }
  .features-block-intro {
    h2 {
      @apply text-white font-heading-serif;
    }
  }
  .features-block-callout {
    > p {
      @apply text-white font-heading;
    }
  }
}
