/* Swipers  */
.swiper {
  &.swiper-testimonial {
    .swiper-wrapper {
      .swiper-slide {
        @apply opacity-30 transition-opacity duration-300;
        &.swiper-slide-active {
          @apply opacity-100;
        }
      }
    }
    .swiper-button-next-arrow,
    .swiper-button-prev-arrow {
      svg {
        rect {
          @apply transition-all duration-300;
        }
      }
      &:hover {
        svg {
          rect {
            @apply stroke-med-blue;
            stroke-opacity: 100;
          }
        }
      }
    }
    .swiper-pagination {
      @apply bg-card-border h-[2px];
      .swiper-pagination-progressbar-fill {
        @apply bg-med-blue;
      }
    }
  }
  &.swiper-featured-blog {
    picture {
      @apply order-1 lg:order-2;
    }
  }
  .swiper-pagination-bullet {
    @apply bg-transparent border-2 border-med-blue opacity-100;
    &.swiper-pagination-bullet-active {
      @apply bg-med-blue;
    }
  }
  &.swiper-testimonial-carousel-big {
    .swiper-button-disabled {
      @apply opacity-20;
    }
    .swiper-pagination-bullets {
      @screen lg {
        @apply left-0 w-[initial] top-0;
      }
      @apply w-[initial] mx-sp-8 relative;
      .swiper-pagination-bullet {
        @apply bg-primary opacity-10 transition-all duration-500;
        &.swiper-pagination-bullet-active {
          @apply bg-primary opacity-100;
        }
      }
    }
  }
  &.swiper-blog-carousel {
    .swiper-pagination-bullet {
      @apply bg-transparent border-2 border-purple-gradient-end opacity-100;
      &.swiper-pagination-bullet-active {
        @apply bg-purple-gradient-end;
      }
    }
  }
}
/* FAQ Accordion */
.faq-block,
.toc-container,
.references-container,
.iop-overview-accordion {
  .toc-heading,
  .references-heading {
    &:hover {
      .toggle {
        svg {
          @apply -rotate-45;
        }
      }
    }
    .toggle {
      svg {
        @apply transition-transform duration-300;
      }
    }
    &.active {
      .toggle {
        svg {
          @apply rotate-45;
        }
      }
    }
  }
  .accordion-header,
  .accordion-header-iop {
    .vertical {
      @apply transition-transform duration-300 origin-center;
    }
    &.active {
      .vertical {
        @apply scale-y-0;
      }
    }
  }
}
.references-container {
  > div > div > p:last-child {
    @apply mb-0;
  }
}
/* Card Grid */
.border-gradient {
  /* @apply before:absolute before:top-0 before:left-0 before:right-0 before:bottom-0 before:border before:border-transparent before:box-border; */
  /* mask-composite: exclude; */
  @apply flex flex-col relative;
  &:before {
    @apply absolute top-0 left-0 right-0 bottom-0 rounded-md border border-transparent pointer-events-none;
    content: '';
    background: linear-gradient(90deg, #eadaff, #a698ff) border-box;
    -webkit-mask: linear-gradient(#fff 0 0) padding-box,
      linear-gradient(#fff 0 0);
    -webkit-mask-composite: destination-out;
    mask-composite: exclude;
  }
}

/* Remove bottom margin from last element in sections */
.acf-innerblocks-container,
#articleContent .container-sm {
  & > *:last-child {
    @apply mb-0;
  }
  /* TODO Maybe? */
  /* & > *:last-child:not(h1, h2, h3, h4, h5, h6) {
    @apply mb-0;
  } */
}

.divider {
  @apply h-[1px] bg-gradient-to-r from-purple-gradient-start to-purple-gradient-end;
}
/* Footer badges */
.badges {
  a {
    img {
      @apply aspect-square object-contain w-[100px];
    }
  }
}

.footer-links {
  & > {
    a {
      @apply text-white text-h5 no-underline mb-sp-3;
    }
  }
}

.nav-logo {
  svg {
    @apply max-w-full;
  }
}

.preserve-3d {
  transform-style: preserve-3d;
}
.translate-z {
  transform: translate3d(-50%, -50%, -1px);
}

.modal-fade {
  @apply opacity-0 invisible translate-y-sp-2;
  & > {
    * {
      @apply scale-95;
    }
  }
}
.paginationjs-pages {
  ul {
    @apply flex gap-sp-4 list-none p-0 m-0;
    li {
      @apply m-0 leading-none block;
      &.J-paginationjs-page {
        &:not(.active) {
          @apply opacity-60;
        }
      }
      a {
        @apply cursor-pointer;
      }
    }
  }
}
.js-tag-id {
  @apply transition-all duration-300 hover:bg-bright-teal;
  &.active {
    @apply bg-darkest-blue text-white;
  }
}

.custom-scroll {
  & > * {
    @apply mb-sp-6;
  }
  &::-webkit-scrollbar {
    @apply h-sp-2 cursor-pointer;
  }
  &::-webkit-scrollbar-track {
    @apply bg-[rgba(70,73,109,0.15)] my-sp-5 cursor-pointer;
  }
  &::-webkit-scrollbar-thumb {
    @apply bg-hover-blue cursor-pointer;
  }
}
.custom-scroll-vertical {
  & > * {
    @apply mr-sp-6;
  }
  &::-webkit-scrollbar {
    @apply w-sp-2 cursor-pointer;
  }
  &::-webkit-scrollbar-track {
    @apply bg-tag-gray ml-sp-5 cursor-pointer;
  }
  &::-webkit-scrollbar-thumb {
    @apply bg-hover-blue cursor-pointer;
  }
}

