.tabs {
  @apply p-sp-4 duration-300 cursor-pointer;
  &.active {
    @apply opacity-100;
  }
  @apply opacity-30;
}
.tab-contents {
  @apply p-sp-4 duration-1000;
  &:nth-child(1) {
    @apply opacity-100;
  }
  &.active {
    @apply block;
  }
  @apply noshow opacity-0;
  & > {
    & * {
      &:last-child {
        @apply mb-0;
      }
    }
  }
}
.carousel-content {
  & > {
    & * {
      &:last-child {
        @apply mb-0;
      }
    }
  }
}

.wp-block-cover {
  @apply mb-sp-6 rounded-md;
  @screen lg {
    @apply text-p-lg;
  }
  img {
    @apply rounded-md;
  }
}

.wp-block-image {
  &.alignfull {
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    box-sizing: border-box;
    transform: translateX(calc(50vw - 50%));
    max-width: 1280px;
    @apply px-sp-5 lg:px-sp-10
    /* @apply inset-x-1/2 -mx-[50vw] relative w-[1280px]; */;
  }
}
.wp-block-heading:has(img) {
  @apply flex lg:items-center items-start;
  & > img {
    @apply mr-sp-4 mt-sp-1 lg:mt-0;
  }
}

