  @apply bg-[linear-gradient(90deg,#C6AFF3,#E2D3FF)];
}
.families-and-caregivers-slider {
  @apply bg-[linear-gradient(90deg,#E07058,#FF9F81)];
}
.providers-slider {
  @apply bg-[linear-gradient(90deg,#161A3D,#262E6C)];
}

.arrow-slider {
  .arrow-slider-bg {
    @apply fill-white duration-300 transition-all;
  }
  .arrow-slider-arrow {
    @apply fill-med-blue duration-300 transition-all;
  }
  &:hover {
    .arrow-slider-bg {
      @apply fill-med-blue;
    }
    .arrow-slider-arrow {
      @apply fill-white;
    }
  }
}

.toc-underline {
  @apply no-underline;

  background: linear-gradient(to right, transparent, transparent),
    linear-gradient(to right, #1d225f, #1d225f);
  background-size: 100% 2px, 0 2px;
  background-position: 100% 100%, 0 100%;
  background-repeat: no-repeat;
  transition: background-size 700ms;
  display: inline;
  &:hover {
    background-size: 0 2px, 100% 2px;
  }
}

.toggle-button:not(.button-primary) {
  &:before {
    @apply transition-all duration-300;
